@import "./sass-variables.sass";

#header {
    display: flex;
    #headerImageContainer {
        position: relative;
        width: 50vw;
        .blob {
            position: absolute;
            top: 35px;
            right: 50px;
            z-index: 1; }
        #play {
            position: absolute;
            top: 60px;
            right: 50px;
            z-index: 2; }
        .wave {
            position: absolute;
            z-index: 3; }
        #wave1 {
            top: 230px;
            right: 20px; }
        #wave2 {
            top: 320px;
            right: 110px; }
        #wave3 {
            top: 40px;
            right: -700px; }
        #wave4 {
            top: 10px;
            right: -830px; } }

    #headerTextContainer {
        width: 50vw;
        z-index: 5;
        .naviList {
            display: flex;
            list-style: none;
            // justify-content: space-around
            margin: 0px;
            margin-top: 50px;
            padding: 0;
            // border: 2px solid green

            li {
                margin-right: 35px;

                a {
                    color: rgba($cfourth, 0.3);
                    font-size: 20px;
                    font-weight: 700;
                    text-decoration: none;
                    // color: #003399
                    // -webkit-text-fill-color: $cMain
                    -webkit-text-stroke-width: 1px;
                    -webkit-text-stroke-color: $cthird;
 } }                    // text-shadow: -1px -1px 0 #000,  1px -1px 0 #000,-1px 1px 0 #000, 1px 1px 0 #000;

            li:first-child {
                margin-right: 60px; } }

        h1 {
            margin: 0px;
            margin-top: 50px;
            color: $csecond;
            font-size: 90px;
            font-weight: 700;
            line-height: 78px; }
        h3 {
            margin: 0px;
            margin-top: 10px;
            color: $cthird;
            font-size: 28px;
            font-weight: 300; }
        form {
            width: 479px;
            height: 39px;
            margin: 0;
            margin-top: 30px;
            padding: 0;
            background: rgba($clight,0.21);
            border: 1px solid rgba($cfourth, 0.21);
            border-radius: 0;
            box-shadow: 5px 10px 10px rgba($cshadow, 0.06);
            input {
                width: 440px;
                height: 39px;
                padding-left: 20px;
                border: none;
                background: none;
                &::placeholder {
                    font-family: 'Titillium Web', sans-serif;
                    color: rgba($cfourth, 0.41);
                    font-size: 20px;
                    font-weight: 200; } }
            & > div {
                position: relative;
                width: 39px;
                height: 39px;
                background: $cthird;
                box-shadow: 5px 10px 10px rgba($cshadow, 0.06);
                & > div {
                    position: absolute;
                    top: 9px;
                    left: 7px;
                    width: 17.59px;
                    height: 17.59px;
                    background-color: $clight;
                    border-radius: 50%;
                    & > div {
                        position: absolute;
                        top: 11px;
                        left: 16px;
                        transform: rotate(125deg);
                        width: 5.41px;
                        height: 12.18px;
                        background-color: $clight; } } } }
        #albumSliderContainer {
            position: relative;
            display: flex;
            align-items: center;
            margin-top: 50px;
            #albumSliderItemsContainer {
                position: relative;
                display: flex;
                width: 500px;
                height: 280px;
                // border: 4px solid red
                #albumSliderItems {
                    position: absolute;
                    overflow: hidden;
                    display: flex;
                    top: 10px;
                    left: -40px;
                    width: 550px;
                    height: 280px;
                    // border: 3px solid blue
                    // position: absolute
                    #items {
                        position: absolute;
                        top: 12px;
                        left: 45px;
                        display: flex;
                        transition: 0.5s ease-in-out; } } }


            #sliderArrows {
                position: absolute;
                top: 70px;
                left: -60px;
                display: flex;
                justify-content: space-between;
                width: 600px;
                .sliderArrow {
                    // margin: 0 200px
                    color: rgba($cfifth, 0.48);
                    font-size: 60px;
                    font-weight: 700;
                    -webkit-text-stroke-width: 1px;
                    -webkit-text-stroke-color: $cfourth;
                    line-height: 87px;
                    cursor: pointer;
                    &:hover {
                        color: $clight; } } } } } }


