@import "./sass-variables.sass";

#randomSection {
    position: relative;
    // overflow: hidden
    display: flex;
    justify-content: center;
    width: 100%;
    height: 699px;
    margin-top: 90px;
    color: $cthird;

    #randomSectionText {
        position: relative;
        width: 400px;
        text-align: center;
        h2 {
            margin: 0px;
            margin-top: 90px;
            font-size: 42px;
            font-weight: 700; }
        button {
            position: relative;
            width: 351px;
            height: 80px;
            margin-top: 30px;
            background-color: $cthird;
            color: $cthird;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: $clight;
            font-size: 39px;
            font-weight: 700;
            outline: none;
            cursor: pointer;
            z-index: 5; }
        #kostkiNiebieskie {
            position: absolute;
            top: 20px;
            right: 20px;
            z-index: 2; }
        .blob {
            position: absolute;
            z-index: 1; }
        #blob1 {
            transform: rotate(90deg);
            top: -110px;
            right: 420px; }
        #blob2 {
            transform: rotate(90deg);
            top: -400px;
            right: -1100px;
            z-index: 1; }
        .wave {
            position: absolute;
            z-index: 3; }
        #randomWave1 {
            top: 400px;
            right: 680px; }
        #randomWave2 {
            top: 370px;
            right: 560px; }
        #randomWave3 {
            top: -220px;
            right: -150px; }
        #randomWave4 {
            top: 480px;
            right: -400px; }
        #randomWave5 {
            top: 130px;
            right: -500px; }
        #randomWave6 {
            top: 50px;
            right: -600px; } } }



