@import "./sass-variables.sass";

#decadeSlider {
    box-sizing: border-box;
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 90px;
    margin: 0;
    padding: 0;
    margin-top: 90px;
    // border: 3px solid pink
    #decadeListContainer {
        position: relative;
        overflow: hidden;
        width: 900px;
        height: 87px;
        // border: 5px solid black
        ul {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            margin: 0;
            padding: 0;
            list-style: none;
            z-index: 2;
            transition: 0.5s ease;
            // border: 5px solid red
            li {
                min-width: 250px;
                height: 87px;
                margin-left: 50px;
                margin-right: 50px;
                color: rgba($csix, 0.5);
                font-size: 45px;
                font-weight: 700;
                text-align: center;
                line-height: 87px; }

            .active {
                // background-color: $clight
                color: $csecond; } } }
    #activeContainer {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        z-index: 1;
        div {
            height: 87px;
            width: 285px;
            background-color: $clight;
            border: 1px solid $cborder; } }
    #decadeArrows {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        z-index: 3;
        a {
            margin: 0 160px;
            color: rgba($cfifth, 0.48);
            font-size: 60px;
            font-weight: 700;
            text-decoration: none;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: $cfourth;
            line-height: 87px;
            cursor: pointer;
            &:hover {
                color: $clight; } } } }

